import gsap from "gsap";
import { Sprite, Texture } from "pixi.js";
import { CircleBlock } from "./CircleBlock";

export class AnyBlock extends CircleBlock {
    private currentIcon: number;
    private temp:number;
    constructor(x: number, y: number, create: boolean) {
        super(x, y, CircleBlock.ANY_TYPE, create);
        this.reGenerateTextures();
    }
    private reGenerateTextures() {
        if (!this.currentIcon) {
            this.currentIcon = 1;
        } else {
            this.currentIcon++;
        }
        if (this.currentIcon > 7) {
            this.currentIcon = 1;
        }
        this.view.texture = this.getTexture();
        this.view2.texture = this.getTexture2();
        this.temp = 0;
        gsap.to(this, {temp: 1, duration: 0.15, onComplete: ()=> {
            this.reGenerateTextures();
        }});
    }
    override getTexture() {
        return Texture.from(`${this.currentIcon || 1}.png`);
    }
    override getTexture2() {
        return Texture.from(`${this.currentIcon || 1}_.png`);
    }
    override clean() {
        gsap.killTweensOf(this);
        super.clean();
    }
}