import { ScoreItem } from "./items/ScoreItem";

export class ScorePool {
    private all: ScoreItem[];
    constructor(amount: number) {
        this.all = [];
        for (let i = 0; i < amount; i++) {
            this.all.push(new ScoreItem());
        }
    }
    public obtain(): ScoreItem {
        if (this.all.length === 0) {
            console.log("Score pool exceed");
            this.all.push(new ScoreItem());
        }
        return this.all.pop()!;
    }
    public release(b: ScoreItem) {
        b.clean();
        this.all.push(b);
    }
}