import gsap from "gsap";
import { Sprite, Texture } from "pixi.js";
import { CircleBlock } from "./CircleBlock";

export class GoalBlock extends CircleBlock {
    private delay?: gsap.core.Tween;
    constructor(x: number, y: number) {
        super(x, y, CircleBlock.GOAL_TYPE, true);
    }
    public resetDeathCounter() {
        if (this.delay) {
            this.delay.kill();
            this.delay = undefined;
        }
    }
    public startCountDeath() {
        if (this.delay) return;
        this.delay = gsap.delayedCall(4, ()=> {
            this.delay = undefined;
            this.emit("gameover");
        });
    }
    override destroyBody(immidiately?: boolean): void {
        this.resetDeathCounter();
        this.off("gameover");
        super.destroyBody(immidiately);
    }
    override setSkull() {
        super.setSkull();
        this.view.texture = Texture.from(`3001.png`);
        this.view2.texture = Texture.from(`3001_.png`);
    }
    protected createView() {
        return new Sprite(this.getTexture());
    }
    protected createView2() {
        return new Sprite(this.getTexture2());
    }
    override getTexture() {
        return Texture.from(`hero.png`);
    }
    override getTexture2() {
        return Texture.from(`hero_.png`);
    }
    protected getRadius() {
        return 50;
    }
    public isGoal() {
        return true;
    }
}