import gsap from "gsap";
import { Container, Sprite, Texture } from "pixi.js";
import { ExplosionsPool } from "./pools/ExplosionsPool";

export class Explosion extends Container{
    private art: Sprite;
    constructor() {
        super();
        this.art = Sprite.from(ExplosionsPool.getTexture(0));
        this.art.anchor.set(0.5,0.5);
        this.addChild(this.art);
        this.scale.set(0, 0);
    }
    
    public async animate(delay: number = 0) {
        return new Promise<void>((resolve)=>{
            let frame = 0;
            const p = {a: 0};
            gsap.to(p, {a: 1, delay: delay, duration: delay+0.4, onUpdate: ()=> {
                const nf = Math.floor(p.a * 2);
                if (nf !== frame) {
                    frame = nf;
                    this.art.texture = ExplosionsPool.getTexture(frame);
                }
            }});

            gsap.to(this.scale, {x: 1, y: 1, duration: delay+0.1});
            gsap.to(this.scale, {x: 1.3, y: 1.3, delay: delay+0.2, duration: 0.3});
            gsap.to(this, {alpha: 0, delay:delay+0.2, duration: 0.5, onComplete: ()=> {
                resolve();
            }});
        });
        
    }
    public clean() {
        this.art.texture = ExplosionsPool.getTexture(0);
        this.scale.set(0, 0);
        this.alpha = 1;
        this.visible = true;
        gsap.killTweensOf(this.scale);
        gsap.killTweensOf(this);
    }
}