import { Loader } from "@pixi/loaders";
import { sounds } from "../core/SoundManager";
import { sendError } from "..";
import { BaseGame, gameSettings, ICleanUnusedParams, stateManager, world } from "../core/BaseGame";
import { finishLoading, getStorageData, startLoading } from "../core/natives/SocialManager";
import { MenuState } from "./states/MenuState";
import { GameViewState } from "./states/GameViewState";
import * as WebFont from "webfontloader";
import gsap from "gsap";
import { ComicsState } from "./states/ComicsState";

export const calculateStars = (score: number, conditions: number[]): number => {
  let stars = 0;
  for (let i = 0; i < conditions.length; i++) {
      if (score > conditions[i]) {
          stars++;
      } else {
          break;
      }
  }
  return stars;
}
export const setSlowMo = (slowmo: boolean, physicsOnly: boolean = false)=> {
  gameSettings.slowmo = slowmo;
  if (!physicsOnly) {
    gsap.globalTimeline.timeScale(gameSettings.slowmo ? 0.4 : 1);
  }
  
}
export default class Game extends BaseGame {
  constructor() {
    super(720, 1500);
  }
  protected async createStates() {
    const skip = await getStorageData("skipcomics","0");
    stateManager.add(new MenuState());
    stateManager.add(new GameViewState());
    if (skip === "0") {
      stateManager.add(new ComicsState());
      stateManager.start(ComicsState);
    } else {
      stateManager.start(MenuState);
    }
    
  }
  protected initContactListener() {
    world.on("endContact", (evt) => {
      if (!evt.shapeA.data || !evt.shapeB.data) return;
      if (stateManager.activeState instanceof GameViewState) {
        stateManager.activeState.endContact(evt);
      }
    });
    world.on("beginContact", (evt) => {
      if (stateManager.activeState instanceof GameViewState) {
        stateManager.activeState.beginContact(evt);
      }
    });
  }
  protected preCleanUnused(step: number): ICleanUnusedParams {
    const state = stateManager.activeState as GameViewState;
    if (state && state instanceof GameViewState) {
      return state.preCleanUnused(step);
    } else {
      return {step, other: undefined}
    }
  }
  protected postCleanUnused(params: ICleanUnusedParams) {
    const state = stateManager.activeState as GameViewState;
    if (state && state instanceof GameViewState) {
      state.postCleanUnused(params);
    }
  }
  protected async load() {
    return new Promise<void>((resolve, reject) => {
      sounds.init("assets/sounds");
      startLoading();
      const loader = new Loader();
      loader.add("base0", "assets/atlases/base0.json");
      loader.add("base1", "assets/atlases/base1.json");
      loader.add("levels", "assets/levels.json");
      loader.add("particles", "assets/emitter.json");
      sounds.add("music", { autoplay: true, loop: true, volume: 0.6 });
      sounds.add("ball hit 0", { volume: 0.4 });
      sounds.add("ball hit 1", { volume: 0.4 });
      sounds.add("ball hit 2", { volume: 0.4 });

      loader.onProgress.add((l) => {
        //loadingProgress(l.progress);
      });
      loader.onError.add((a, b, c) => {
        sendError({ error: `loading (${c.url})` });
        reject();
      });

      loader.load(async () => {
        await this.loadFonts();
        gameSettings.levels = loader.resources.levels.data.levels;
        gameSettings.particles = loader.resources.particles.data;
        await this.startAfterLoading();
        resolve();
      });
    });
  }
  private async loadFonts() {
    return new Promise<void>((resolve) => {
      WebFont.load({
        custom: {
            families: ["FontsFree-Net-AmericanTypewriterBold"],
            urls: ["assets/fonts/font.css"],
        },
        active: () => {
            resolve();
        },
        timeout: 60000,
    });
    });
  }
  private async startAfterLoading() {
    const preloader = document.getElementById("preloader");
    if (preloader) {
      preloader.style.display = "none";
    }
    await finishLoading();
    //await leaderboards.load();
  }
}
