import { Texture } from "pixi.js";
import { Explosion } from "../Explosion";

export class ExplosionsPool {
    private static _textures;
    private all: Explosion[];
    constructor(amount: number) {
        this.all = [];
        for (let i = 0; i < amount; i++) {
            this.all.push(new Explosion());
        }
    }
    public obtain(): Explosion {
        if (this.all.length === 0) {
            console.log("Missles pool exceed");
            this.all.push(new Explosion());
        }
        return this.all.pop()!;
    }
    public release(b: Explosion) {
        b.clean();
        this.all.push(b);
    }

    public static getTexture(id: number) {
        return ExplosionsPool._textures[id];
    }
    public static createTextures() {
        if (!ExplosionsPool._textures) {
            ExplosionsPool._textures = [Texture.from("game_explosion_0.png"), Texture.from("game_explosion_1.png"), Texture.from("game_explosion_2.png")]
        }
    }
}