import { Body, Box, Shape } from "p2";
import { Sprite, Texture } from "pixi.js";
import { METER_SIZE } from "../../../core/BaseGame";
import { PhysicsSprite } from "../../../core/PhysicsSprite";
import gsap, {Cubic} from "gsap";

export class RocketPowerup extends PhysicsSprite {
    private startX: number;
    private startY: number;
    private endX: number;
    private endY: number;
    constructor() {
        super(300);
        this.createBody(Body.DYNAMIC);
        this.reset();
    }
    override createShape(): Shape {
        const s = new Box({width: 200 / METER_SIZE, height: 200 / METER_SIZE});
        s.sensor = true;
        return s;
    }
    protected createView(): Sprite {
        return new Sprite(Texture.from(`powerup_rocket.png`));
    }
    public async animate(startX: number, startY: number, endX: number, endY: number) {
        return new Promise<void>((r)=>{
            this.visible = true;
            this.startX = startX;
            this.startY = startY;
            this.endX = endX;
            this.endY = endY;
            this.position.set(startX, startY);
            if (startX !== endX) this.view.angle = 90;
            const s = {x: this.startX, y: this.startY};
            this.body!.position = [s.x/METER_SIZE, s.y/METER_SIZE];
            gsap.to(s, {x: this.endX, y: this.endY, duration: 1, ease: Cubic.easeIn, onComplete: ()=>{
                this.reset();
                r();
            }, onUpdate: ()=> {
                this.body!.position = [s.x/METER_SIZE, s.y/METER_SIZE];
            }});
        });
    }
    private reset() {
        this.body!.gravityScale = 0;
        this.visible = false;
        this.body!.position = [-2000/METER_SIZE, -2000/METER_SIZE];
    }
}