import { Sprite, Texture } from "pixi.js";

export class MisslesPool {
    private all: Sprite[];
    constructor(amount: number) {
        this.all = [];
        for (let i = 0; i < amount; i++) {
            this.all.push(MisslesPool.createMissle());
        }
    }
    public obtain(): Sprite {
        if (this.all.length === 0) {
            console.log("Missles pool exceed");
            this.all.push(MisslesPool.createMissle());
        }
        return this.all.pop()!;
    }
    public release(b: Sprite) {
        this.all.push(b);
    }
    private static createMissle() {
        return new Sprite(Texture.from(`like.png`));
    }
}