import { Sprite, Texture } from "pixi.js";
import { gameSettings, METER_SIZE, world } from "../../../core/BaseGame";
import gsap from "gsap";
import { Body, Circle, Shape } from "p2";
import { ECollisionGroups, PhysicsSprite } from "../../../core/PhysicsSprite";
import { explosionsPool } from "../GameViewState";
//import { GlowFilter } from "@pixi/filter-glow";

export class CircleBlock extends PhysicsSprite {
    public static BALL_SCORE = 5;
    public static BONUS_SCORE = 100;
    public static EXPLOSION_SCORE = 50;

    public static GOAL_TYPE = 1000;
    public static END_TYPE = 500;
    public static STATIC_OBSTACLE_TYPE = 250;
    public static RANDOM_OBSTACLE_TYPE = 251;
    public static BOMB_TYPE = 2000;
    public static SKULL_TYPE = 3000;
    public static RAINBOW_TYPE = 4000;
    public static ANY_TYPE = 5000;

    protected static SCALE_FACTOR = 0.5;

    public sameColorNeighbors: Map<CircleBlock, boolean> = new Map<CircleBlock, boolean>();
    public steps = 1;

    protected stepsAmount = 3;
    protected view2: Sprite;
    
    private savedType = -1;
    private skullMeter = 1;
    private _kinematic = false;
    private permanentSkull = false;
    private busy = false;
    private sensorShape: Shape;

    constructor(x: number, y: number, type: number, create: boolean = true) {
        super(type);
        this.permanentSkull = type === CircleBlock.SKULL_TYPE;

        this.position.set(x,y);
        if (create) {
            this.createBody(Body.DYNAMIC);
        }
        this.view2 = this.createView2();
        this.view2.anchor.set(0.5, 0.5);
        this.addChild(this.view2);
    }
    public rotateView(angle: number) {
        this.view.rotation = angle;
    }
    public get kinematic() {
        return this._kinematic;
    }
    public set kinematic(b: boolean) {
        this._kinematic = b;
    }
    override createBody(physType: 1 | 2 | 4, mass = 5): void {
        super.createBody(physType, mass);
        this.sensorShape = new Circle({radius: (this.getRadius() + 5) / METER_SIZE});
        this.sensorShape.sensor = true;
        this.sensorShape.material = gameSettings.material;
        (this.sensorShape as any).data = true;
        this.body?.addShape(this.sensorShape);
    }
    public enableGravity() {
        if (!this.body) return;
        this.body.gravityScale = 1;
        this.body.mass = 5;
        this.body.updateMassProperties();
        this.shape.material = gameSettings.material;
    }
    public animateAndDestroy() {
        this.killTween();
        gsap.to(this.view.scale, {x: 0, y: 0, duration: 0.2, onComplete: ()=> {
            this.clean();
        }});
        gsap.to(this.view2.scale, {x: 0, y: 0, duration: 0.2});
        this.destroyBody();
    }
    private killTween() {
        gsap.killTweensOf(this.view.scale);
        gsap.killTweensOf(this.view2.scale);
    }
    override clean(): void {
        this.busy = false;
        super.clean();
    }
    public decreaseSkullMeter() {
        if (this.type === CircleBlock.SKULL_TYPE && !this.permanentSkull) {
            this.skullMeter -= 1;
            if (this.skullMeter === -1) {
                this.removeSkull();
            }
        }
        
    }
    public isSkull(): boolean {
        return this.type === CircleBlock.SKULL_TYPE;
    }
    public setSkull() {
        this.savedType = this.type;
        this.skullMeter = 3;
        this.kinematic = false;
        if (this.body) this.body.gravityScale = 1;
        this.type = CircleBlock.SKULL_TYPE;
        this.view.texture = this.getTexture();
        this.view2.texture = this.getTexture2();
    }
    public removeSkull() {
        this.type = this.savedType;
        this.savedType = -1;
        this.view.texture = this.getTexture();
        this.view2.texture = this.getTexture2();
        this.view2.visible = true;
    }
    public setBomb() {
        if (this.busy) return;
        this.type = CircleBlock.BOMB_TYPE;
        this.view.texture = Texture.from("bomb.png");
        this.view2.visible = false;
        this.killTween();
        this.busy = true;
        gsap.to(this.view.scale, {x: 0.4, y: 0.4, duration: 1, onUpdate: ()=> {
            this.updateShape();
        }});
        gsap.to(this.view.scale, {x: 1.2, y: 1.2, delay: 1, duration: 0.2, onUpdate: ()=> {
            this.updateShape();
        }, onComplete: ()=> {
            const explosion = explosionsPool.obtain();
            this.addChild(explosion);
            explosion.animate().then(()=> {
                explosionsPool.release(explosion);
            });
            this.animateAndDestroy();
        }});
    }
    public boom(fromBoom: boolean = true) {
        return this.increase(this.stepsAmount-this.steps, fromBoom);
    }
    protected getRadius() {
        return 35;
    }
    protected getTexture() {
        return Texture.from(`${this.type}.png`);
    }
    protected getTexture2() {
        return Texture.from(`${this.type}_.png`);
    }
    protected createView() {
        const v = new Sprite(this.getTexture());
        v.scale.set(0.5,0.5);
        return v;
    }
    protected createView2() {
        const v = new Sprite(this.getTexture2());
        v.scale.set(0.5,0.5);
        return v;
    }
    override createShape(): Shape {
        const shape = new Circle({radius: this.getRadius() / METER_SIZE});
        //shape.collisionGroup = ECollisionGroups.BALLS;
        //shape.collisionMask = ECollisionGroups.BALLS | ECollisionGroups.MOVER | ECollisionGroups.OBSTACLES | ECollisionGroups.WALLS;
        shape.material = gameSettings.material;
        return shape;
    }
    
    public increase(s: number, fromBoom: boolean = false): number {
        if (this.busy || !this.body) return 0;
        let killAmount = 0;
        this.busy = true;
        const oldSteps = this.steps;
        s += Math.max(0, this.sameColorNeighbors.size - 1);
        this.steps += s;
        if (this.steps >= this.stepsAmount) {
            if (fromBoom) {
                killAmount = 1;
            }
            this.killTween();
            this.sameColorNeighbors.forEach((n,k)=> {
                if (k !== this) {
                    const a = k.increase(this.stepsAmount-k.steps, fromBoom);
                    if (fromBoom) {
                        killAmount += a;
                    }
                }
            });
            if (oldSteps < 2) {
                gsap.to(this.view.scale, {x: 0, y: 0, duration: 0.2, onComplete: ()=> {
                    this.clean();
                }});
                gsap.to(this.view2.scale, {x: 0, y: 0, duration: 0.2});
            } else {
                let radius = this.view.width / 2;
                let a = 0;
                gsap.to(this.view.scale, {x: 0, y: 0, duration: 0.3, onUpdate: ()=> {
                    this.view.position.set(Math.cos(a)*radius, Math.sin(a)*radius);
                    a += Math.PI/15;
                    radius -= 1;
                }, onComplete: ()=> {
                    this.clean();
                }});
                this.view2.scale.set(0,0);
            }
            
            this.destroyBody(fromBoom);
        } else {
            const i = CircleBlock.SCALE_FACTOR * this.steps;
            gsap.to(this.view.scale, {x: i, y: i, duration: 0.2, onUpdate: ()=> {
                this.updateShape();
            }, onComplete: ()=> {
                this.busy = false;
            }});
            gsap.to(this.view2.scale, {x: i, y: i, duration: 0.2});
        }
        return killAmount;
    }
    private updateShape() {
        (this.shape as Circle).radius = this.getRadius() / CircleBlock.SCALE_FACTOR * this.view.scale.x / METER_SIZE;
        this.shape.updateBoundingRadius();
        (this.sensorShape as Circle).radius = (this.getRadius() + 5) / CircleBlock.SCALE_FACTOR * this.view.scale.x / METER_SIZE;
        this.sensorShape.updateBoundingRadius();
        this.body?.updateBoundingRadius();
        this.body?.updateMassProperties();
    }
}