import { Bolt } from "./items/Bolt";

export class BoltsPool {
    private all: Map<number, Bolt[]>;
    constructor() {
        const types = 4;
        const amount = 20;
        this.all = new Map<number, Bolt[]>();
        for (let t = 0; t < types; t++) {
            const arr: Bolt[] = [];
            for (let i = 0; i < amount; i++) {
                arr.push(new Bolt(t));
            }
            this.all.set(t, arr);
        }
    }
    public obtain(type: number): Bolt {
        const arr = this.all.get(type);
        if (arr && arr.length === 0) {
            console.log("Bolts pool exceed");
            arr.push(new Bolt(type));
        }
        return arr?.pop()!;
    }
    public release(b: Bolt) {
        const arr = this.all.get(b.id);
        arr!.push(b);
    }
}