import gsap, {Expo} from "gsap";
import { Container, Text } from "pixi.js";

export class ScoreItem extends Container {
    private txt: Text;
    private score: number;
    constructor() {
        super();
        this.txt = new Text("", {fontFamily: "FontsFree-Net-AmericanTypewriterBold", fontSize: 40, fill: 0x3e2996, align: "center"});
        this.txt.anchor.set(0.5, 0.5);
        this.addChild(this.txt);
    }
    public start(x: number, y: number, score: number) {
        this.score = score;
        this.txt.text = "+" + score.toString();
        this.x = x;
        this.y = y;
        gsap.to(this, {x: 0, y: -700, duration: 0.7, delay: Math.random()*0.15, ease: Expo.easeIn, onComplete: ()=> {
            this.finish();
        }});
    }
    public finish() {
        gsap.killTweensOf(this);
        this.emit("done", this.score);
    }
    public clean() {
        gsap.killTweensOf(this);
        this.off("done");
        this.score = 0;
        this.alpha = 1;
        this.txt.text = "";
    }
}